.img-label-wrap {
  position: relative;
  display: inline-block;
  position: relative;
  border: 1px solid #ccc;
  padding: 0 !important;
  margin: 0 !important;
  background: #fff;

  .img {
    display: block;
  }

  .label-list {
    .label-item {
      position: absolute;
      background: RGBA(0, 0, 0, 0.35);
      color: #fff;
      border-radius: 5px;
      cursor: pointer;
      user-select: none;
      transition: 0.3s all;

      .label-text {
        display: inline-block;
        padding: 7px 10px;
        outline: none;
      }

      &:hover {
        background: RGBA(0, 0, 0, 0.5);

        &::after {
          border-color: RGBA(0, 0, 0, 0.5) RGBA(0, 0, 0, 0) RGBA(0, 0, 0, 0);
        }

        &::before {
          display: block;
          background: rgba(0, 0, 0, 1);
        }
      }

      &::after {
        position: absolute;
        bottom: -10px;
        left: 50%;
        transform: translate(-50%, 0);
        content: ' ';
        display: block;
        width: 0;
        height: 0;
        transition: 0.3s all;
        border-width: 5px;
        border-style: solid;
        border-color: RGBA(0, 0, 0, 0.35) RGBA(0, 0, 0, 0) RGBA(0, 0, 0, 0);
      }

      &::before {
        display: none;
        position: absolute;
        right: -6px;
        top: -6px;
        content: 'x';
        width: 16px;
        height: 16px;
        line-height: 12px;
        font-size: 12px;
        text-align: center;
        background: rgba(0, 0, 0, 0.6);
        border-radius: 50%;
        transition: 0.3s all;
      }
    }
  }

  .menu-list {
    position: absolute;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    background: #fff;
    overflow: hidden;

    .menu-item {
      padding: 10px;
      font-size: 14px;
      transition: 0.2s all;
    }

    .menu-item + .menu-item {
      border-top: 1px solid #ccc;
    }
  }
}


